import config from './config.json';

class ApiFetcher {
    static ADDITIONAL_CATEGORY_USED_LIKE_NEW = 1;
    static ADDITIONAL_CATEGORY_USED_VERY_GOOD = 2;
    static ADDITIONAL_CATEGORY_USED_GOOD = 3;
    static ADDITIONAL_CATEGORY_USED_ACCEPTABLE = 4;
    static ADDITIONAL_CATEGORY_HISTORICAL_DATA = 5;

    static API_CREDENTIALS = undefined; // must be "include" if the same origin

    static API_URL = config.API_DEVEL_MODE ? (config.API_DEVEL_LOCAL ? config.API_DEVELOPMENT_URL_LOCAL : config.API_DEVELOPMENT_URL) : config.API_PRODUCTION_URL

    static redirectToLoginPage () {
        window.location.replace("https://membercentral.net/app/index?redirect=" + encodeURIComponent(window.location.href))
    }

    static async getPriceHistory(isbn) {
        return new Promise((resolve, reject) => {
            let url = (ApiFetcher.API_URL + config.API_ENDPOINT_PRICE_HISTORY).replace("{{isbn}}", isbn);
            fetch(url, {credentials: ApiFetcher.API_CREDENTIALS}).then(response => response.json().then(data => {
                if (data.status === 200) {
                    resolve(data.data)
                } else if (data.status === 401) {
                    ApiFetcher.redirectToLoginPage();
                } else {
                    data.error_message = "Could not get price history. Refresh the page!"
                    reject(data);
                }
            })).catch(reason => reject({error_message: reason, status: 500}));
        });
    }

    static async getPriceHistoryAdditional (isbn, category) {
        return new Promise((resolve, reject) => {
            let url = (ApiFetcher.API_URL + config.API_ENDPOINT_PRICE_HISTORY_ADDITIONAL).replace("{{isbn}}", isbn).replace("{{category}}", category);

            fetch(url, {credentials: ApiFetcher.API_CREDENTIALS}).then(response => response.json().then(data => {
                if (data.status === 200) {
                    resolve(data.data)
                } else {
                    data.error_message = "Could not get additional history data. Refresh the page!"
                    reject(data);
                }
            })).catch(reason => reject({error_message: reason, status: 500}));
        });
    }

    static async getTrackProduct (isbn) {
        return new Promise((resolve, reject) => {
            let url = (ApiFetcher.API_URL + config.API_ENDPOINT_TRACK_PRODUCT).replace("{{isbn}}", isbn);

            fetch(url, {credentials: ApiFetcher.API_CREDENTIALS}).then(response => response.json().then(data => {
                if (data.status === 200) {
                    resolve(data.data)
                } else {
                    data.error_message = "Could not get additional history data. Refresh the page!"
                    reject(data);
                }
            })).catch(reason => reject({error_message: reason, status: 500}));
        })
    }

    static async updateTrackProduct (data) {
        var formData = new FormData();

        Object.keys(data).forEach(key => {
            if (typeof data[key] === 'object') {
                Object.keys(data[key]).forEach(key2 => {
                    formData.append(key + "[]", data[key][key2])
                });
            } else {
                formData.append(key, data[key]);
            }
        });

        return new Promise((resolve, reject) => {
            fetch(ApiFetcher.API_URL + config.API_ENDPOINT_TRACK_PRODUCT_PUT, {
                method: 'POST',
                cache: 'no-cache',
                credentials: ApiFetcher.API_CREDENTIALS,
                body: formData
            }).then(response => {
                response.json().then(data => {
                if (data.status === 200) {
                    resolve(data.data)
                } else {
                    data.error_message = "Could not get additional history data. Refresh the page!"
                    reject(data);
                }
            })}).catch(reason => reject({error_message: reason, status: 500}));
        })
    }
}

export default ApiFetcher;